@import url(https://fonts.googleapis.com/css2?family=Varela+Round&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: 'Varela Round', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  width: 100%;
  height: 100vh;

  background-color: #142850;
  color: #dae1e7;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

a,
button {
  font-family: 'Varela Round', sans-serif;
  color: #dae1e7;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App h1 {
  font-size: 3rem;
}

.App h2 {
  opacity: 0.7;
  font-weight: 500;
  margin-bottom: 8px;
}

.App .branding {
  margin-bottom: 64px;
  opacity: 0.5;
}

.App .vertical-sliders-div-container {
  margin: 0 auto;

  display: flex;
  justify-content: center;
}

.App .horizontal-sliders-container {
  width: 410px;
  margin: 0px auto;

  display: flex;
  justify-content: space-around;
}

.App .vertical-sliders {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.copy {
  margin-top: 64px;
  justify-content: center;
  align-items: center;
  display: flex;
}

.copy .text {
  font-size: 1.2rem;
  margin-right: 8px;
}

.copy .input {
  height: 64px;
  font-size: 1rem;
  width: 400px;
  text-align: center;
  background-color: #27496d;
  border: none;
  border-radius: 16px 0px 0px 16px;
  font-weight: bold;
  color: #dae1e7;
}

.copy .button {
  width: 128px;
  height: 64px;
  font-size: 1.2rem;
  border-radius: 0px 16px 16px 0px;
  border: none;
  cursor: pointer;
  background-color: #00909e;
  color: #dae1e7;
}

.copy .button:active {
  opacity: 0.6;
}

.tooltip {
  position: relative;
  display: inline-block;
}

